*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}

@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/open-sans-300.woff2") format("woff2"), url("../fonts/open-sans-300.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-700.woff2") format("woff2"), url("../fonts/open-sans-700.woff") format("woff");
}
.container {
  outline-color: #6c12e0;
  outline-width: 2px;
  outline-style: dotted;
  outline-offset: 0;
}

.container {
  width: 320px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 660px) {
  .container {
    width: 660px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-right: 130px;
    padding-left: 130px;
  }
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "Open Sans", "Verdana", sans-serif;
  font-size: 14px;
}

.page__header,
.page__footer {
  display: grid;
  grid-template-columns: 1fr 280px 1fr;
  column-gap: 20px;
}
@media (min-width: 660px) {
  .page__header,
  .page__footer {
    grid-template-columns: 1fr 620px 1fr;
    column-gap: 20px;
  }
}
@media (min-width: 1200px) {
  .page__header,
  .page__footer {
    grid-template-columns: 1fr 940px 1fr;
    column-gap: 130px;
  }
}

.page__header {
  position: absolute;
}

.page__nav,
.page__footer-content {
  grid-column: 2/3;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}

.page__title {
  margin: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}
@media (min-width: 660px) {
  .page__title {
    font-size: 40px;
    line-height: 54px;
  }
}
@media (min-width: 1200px) {
  .page__title {
    font-size: 50px;
    line-height: 68px;
  }
}

.header {
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 1199px) {
  .header--active {
    background-color: #283645;
  }
}

.body {
  font-size: 14px;
  line-height: 24px;
}

.footer__content {
  display: grid;
  justify-items: center;
  align-items: center;
  padding: 40px 0;
  grid-template-rows: repeat(3, min-content);
}
@media (min-width: 660px) {
  .footer__content {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: initial;
    padding: 36px 0;
  }
}
@media (min-width: 1200px) {
  .footer__content {
    padding: 71px 0;
  }
}

.footer__logo,
.footer__copyright {
  width: 100%;
  justify-content: center;
  align-items: center;
}

@media (max-width: 659px) {
  .footer__logo {
    margin-bottom: 38px;
  }
}
@media (min-width: 660px) {
  .footer__logo {
    margin-top: -10px;
    justify-content: initial;
    align-items: initial;
  }
}

.footer__list {
  margin: 0;
}

@media (max-width: 659px) {
  .footer__copyright {
    margin-top: 38px;
  }
}
@media (min-width: 660px) {
  .footer__copyright {
    justify-content: right;
  }
}

.logo {
  display: flex;
}
.logo:not(:link) {
  pointer-events: none;
}
.logo:hover {
  filter: brightness(0) saturate(100%) invert(24%) sepia(48%) saturate(4325%) hue-rotate(328deg) brightness(86%) contrast(91%);
}
.logo--header:active {
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(0%) hue-rotate(116deg) brightness(103%) contrast(105%) opacity(30%);
}
.logo--footer:active {
  filter: opacity(30%);
}

.logo__image {
  display: block;
}
@media (min-width: 660px) {
  .logo__image {
    width: 145px;
  }
}
@media (min-width: 1200px) {
  .logo__image {
    width: 147px;
  }
}

.hero {
  color: #ffffff;
  background-color: #1d2631;
}

.hero__button {
  width: 100%;
  margin-bottom: 20px;
}
@media (min-width: 660px) {
  .hero__button {
    margin-bottom: 34px;
  }
}
@media (min-width: 1200px) {
  .hero__button {
    margin-bottom: 55px;
  }
}

.hero__list {
  margin: 0;
  display: flex;
  align-items: center;
  padding: 0;
  list-style-type: none;
}

.hero__item {
  display: block;
}

.hero__link {
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}
.hero__link--apple {
  width: 83px;
  height: 80px;
  background-image: url("../img/logos/apple.svg");
}
.hero__link--google {
  width: 79px;
  height: 80px;
  background-image: url("../img/logos/android.svg");
}
.hero__link--ms {
  width: 80px;
  height: 80px;
  background-image: url("../img/logos/microsoft.svg");
}

.hero__note {
  font-weight: 300;
  margin: 16px 0 0;
  text-align: center;
}
@media (min-width: 660px) {
  .hero__note {
    margin: 26px 0 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .hero__note {
    margin: 46px 0 0;
  }
}

.hero-index {
  display: grid;
  grid-template-columns: 1fr 280px 1fr;
  grid-template-rows: 462px 1fr;
  column-gap: 20px;
  background-image: url("../img/index/promo-hand-mobile@1x.png"), url("../img/index/promo-back-mobile@1x.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: 50% 176px, center 0;
  background-size: auto, auto 462px;
}
@media (min-width: 660px) {
  .hero-index {
    grid-template-columns: 1fr repeat(2, 300px) 1fr;
    grid-template-rows: min-content;
    min-height: 729px;
    background-image: url("../img/index/promo-hand-tablet@1x.png"), url("../img/index/promo-back-tablet@1x.jpg");
    background-position: 50% bottom, center 0;
    background-size: auto, cover;
  }
}
@media (min-width: 1200px) {
  .hero-index {
    grid-template-columns: 1fr repeat(3, 300px) 1fr;
    min-height: 853px;
    background-image: url("../img/index/promo-hand-desktop@1x.png"), url("../img/index/promo-back-desktop@1x.jpg");
    background-position: calc(50% + 70px) bottom, center 0;
  }
}

.hero-index__content {
  grid-column: 2/3;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 31px;
}
@media (min-width: 1200px) {
  .hero-index__content {
    padding-top: 51px;
  }
}

.hero-index__title {
  grid-column: 2/3;
  padding-top: 95px;
  overflow-y: hidden;
}
@media (min-width: 660px) {
  .hero-index__title {
    overflow-y: initial;
    line-height: 48px;
    padding-top: 200px;
  }
}
@media (min-width: 1200px) {
  .hero-index__title {
    line-height: 68px;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 1200px) {
  .nav {
    justify-content: space-between;
    flex-direction: row;
  }
}

.nav__top-bar {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 66px;
  align-items: center;
}
@media (min-width: 660px) {
  .nav__top-bar {
    height: 110px;
  }
}
@media (min-width: 1200px) {
  .nav__top-bar {
    width: fit-content;
    flex-shrink: 0;
  }
}

@media (min-width: 660px) {
  .nav__logo {
    margin-top: -9px;
  }
}

.nav__list {
  position: absolute;
  left: 0;
  right: 0;
  top: 66px;
  display: none;
  flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: #283645;
  list-style-type: none;
}
@media (min-width: 660px) {
  .nav__list {
    top: 110px;
  }
}
@media (min-width: 1200px) {
  .nav__list {
    position: unset;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: end;
    margin-right: -40px;
    padding: 22px 0 16px;
    background-color: transparent;
  }
}

.nav__item {
  position: relative;
  display: flex;
  align-items: flex-start;
}
@media (max-width: 1199px) {
  .nav__item::before, .nav__item::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
  }
  .nav__item::before {
    top: 1px;
    background-color: rgba(255, 255, 255, 0.15);
  }
  .nav__item::after {
    background-color: #000000;
  }
  .nav__item:last-of-type {
    box-shadow: 0 1px 0 #000000;
  }
}

.nav__link {
  width: 100%;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 1200px) {
  .nav__link {
    font-weight: 400;
    padding: 20px 40px;
    text-transform: initial;
  }
}
.nav__link:hover {
  color: #d22856;
}
.nav__link:active {
  color: rgba(255, 255, 255, 0.3);
}
.nav__link--current {
  position: relative;
  pointer-events: none;
}
@media (max-width: 1199px) {
  .nav__link--current {
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 1200px) {
  .nav__link--current::after {
    content: "";
    position: absolute;
    bottom: 7px;
    left: 40px;
    right: 40px;
    height: 2px;
    background-color: #ffffff;
  }
}

.nav__toggler {
  border: none;
  padding: 0;
  height: 66px;
  margin-right: -20px;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .nav__toggler {
    display: none;
  }
}
.nav__toggler:hover {
  cursor: pointer;
  background-color: #d22856;
}
.nav__toggler:active {
  background-color: rgba(210, 40, 86, 0.3);
}
.nav__toggler--close {
  width: 63px;
  mask: url("../img/ico/btn-menu-close.svg") no-repeat center;
}
.nav__toggler--open {
  width: 90px;
  mask: url("../img/ico/btn-menu-open.svg") no-repeat center;
}

.advantages__list-wrapper {
  background-color: #f2f2f2;
}

.advantages__list {
  list-style-type: none;
}

.advantages__item--add {
  display: none;
}
@media (min-width: 660px) {
  .advantages__item--add {
    display: inherit;
  }
}

.advantages__name {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}
@media (min-width: 1200px) {
  .advantages__name {
    font-size: 30px;
    line-height: 1;
  }
}
.advantages__name--add {
  font-size: 30px;
  line-height: 1;
}

@media (min-width: 1200px) {
  .advantages__additional {
    background-color: #f2f2f2;
  }
}

.rates {
  background-color: #f2f2f2;
}

.map {
  height: 200px;
  overflow: hidden;
}
@media (min-width: 660px) {
  .map {
    height: 374px;
  }
}
@media (min-width: 1200px) {
  .map {
    height: 502px;
  }
}

.map__image {
  width: 100%;
  background-size: cover;
}

.page-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 18px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  background-color: transparent;
  border-radius: 30px;
  outline-width: 3px;
  outline-style: solid;
  outline-offset: -3px;
}
.page-button--hero {
  color: #ffffff;
  outline-color: #ffffff;
}
.page-button--hero:hover {
  color: #d22856;
  background-color: #ffffff;
}
.page-button--hero:active {
  color: rgba(210, 40, 86, 0.3);
}

.social-networks {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 8px;
  padding: 0;
  list-style-type: none;
}

.social-networks__link {
  display: flex;
  width: 54px;
  height: 54px;
  align-items: center;
  justify-content: center;
  color: #d22856;
  border-radius: 50%;
  outline: 3px solid rgba(40, 54, 69, 0.1);
  outline-offset: -3px;
}
.social-networks__link svg {
  color: #d22856;
}
.social-networks__link:hover {
  outline-color: #283645;
}
.social-networks__link:active {
  outline-color: rgba(40, 54, 69, 0.1);
}
.social-networks__link:active svg {
  color: rgba(210, 40, 86, 0.3);
}

.copyright {
  display: flex;
  line-height: 18px;
  text-decoration: none;
  word-break: break-word;
  color: #283645;
}
@media (min-width: 1200px) {
  .copyright {
    font-size: 18px;
  }
}
.copyright:focus-visible {
  outline: 3px solid #1c9b5e;
}

.copyright__logo {
  color: #d22856;
  flex-shrink: 0;
  margin-left: 15px;
}
.copyright:hover .copyright__logo {
  color: #283645;
}
.copyright:active .copyright__logo {
  color: rgba(40, 54, 69, 0.3);
}